import React, { useState, useEffect } from "react";
import {
  CButton,
  CCol,
  CModal,
  CModalBody,
  CModalFooter,
  CModalHeader,
  CModalTitle,
  CRow,
} from "@coreui/react";
import "./index.scss";
import { onClose, refreshPage } from "./services.js";
import { useDispatch } from "react-redux";
import { STATUS } from "apiServices/action";

const PopUp = ({
  refresh,
  status,
  icon,
  statusDisplay = false,
  title = "",
  cancelButtonTitle = "Cancel",
  actionButtonTitle = "Submit",
  cancelButtonClass = "bg-background",
  actionButtonClass = "bg-background",
  actionButtonEvent = null,
  colorText = "red",
  msg1 = null,
  msg2 = null,
  onCloseFunction = null,
  refreshOnCancel = true,
  setIsSuccess
}) => {
  const dispatch = useDispatch();
  if (status?.code === 500 ) {
    icon = "em-pensive"
    title = "Server Error";
    cancelButtonTitle = "Try Again";
    actionButtonTitle = "Contact Administration";
    colorText = "red";
    msg1 = status?.message;
    msg2 =
      "Please try again or contact Sayurbox administration for further information.";
    cancelButtonClass = "bg-green";
    statusDisplay = true;
    refreshOnCancel = true;
  }
  if (status?.code === 408 ) {
    icon = "em-alarm_clock"
    title = "Request Timeout";
    cancelButtonTitle = "Try Again";
    actionButtonTitle = "Contact Administration";
    colorText = "red";
    msg1 = status?.message;
    msg2 =
      "Please try again or contact Sayurbox administration for further information.";
    cancelButtonClass = "bg-green";
    statusDisplay = true;
    refreshOnCancel = true;
  }
  if (status?.code === 400 ) {
    icon = "em-alarm_clock"
    title = "Server Error";
    cancelButtonTitle = "Try Again";
    actionButtonTitle = "Contact Administration";
    colorText = "red";
    msg1 = status?.message;
    msg2 =
      "Please try again or contact Sayurbox administration for further information.";
    cancelButtonClass = "bg-green";
    statusDisplay = true;
    refreshOnCancel = false;
  }
  if (status?.code === 404) {
    title = "Something wrong with the application";
    cancelButtonTitle = "Try Again";
    actionButtonTitle = "Contact Administration";
    colorText = "red";
    msg1 = status?.message;
    msg2 =
      "Please try again or contact Sayurbox administration for further information.";
    cancelButtonClass = "bg-green";
    statusDisplay = true;
    refreshOnCancel = false;
  }

  const [show, setShow] = useState(statusDisplay);

  useEffect(() => {
    //from outside change
    setShow(statusDisplay);
  }, [statusDisplay]);

  useEffect(() => {
  }, [show]);

  window.onkeydown = e => e.keyCode === 27 ? setShow(false) : null
  return (
    <CRow className={`${show ? null : "d-none"}`}>
      <CCol>
        <CModal
          show={show}
          closeOnBackdrop={false}
          onClose={() => {
            setShow(false)
            if (typeof (setIsSuccess) === 'function') setIsSuccess({ success: null, name: null })
            dispatch({ type: STATUS, status: null });
            if (onCloseFunction) {
              onCloseFunction()
            }
          }}
          size="lg"
          style={{ minHeight: "280px" }}
          bodyOpenClassName="p-3"
        >
          {/* <div className='d-flex flex-row-reverse'>
            <i className='pr-2 pt-2 iconU-modalClose' onClick={() => setShow(false)}></i>
          </div>
          <CModalHeader className="px-5 pt-3 border-none">
              <CModalTitle className={`text LatoBold ${colorText}`}>
                {title}
              </CModalTitle> */}

          <div className="d-flex flex-row-reverse">
            <i
              className="pr-3 pt-3 iconU-modalClose"
              onClick={() => {
                setShow(false)
                if (typeof (setIsSuccess) === 'function') setIsSuccess({ success: null, name: null })
                dispatch({ type: STATUS, status: null });
                if (onCloseFunction) {
                  onCloseFunction()
                }
              }}
            ></i>
          </div>
          <CModalHeader className="px-5 pt-5 border-none">
            <CModalTitle className={`text LatoBold ${colorText}`}>
              {title} <span><i className={`em ${icon}`}></i></span>
            </CModalTitle>
          </CModalHeader>
          <CModalBody className="px-5 py-0">
            <p className="text mb-2">{msg1}</p>
            <p className="text">{msg2}</p>
          </CModalBody>

          <CModalFooter className="px-5 pb-5 pt-0 justify-content-start border-none">
            {cancelButtonTitle ? (
              <CButton
                className={`${cancelButtonClass} no-shadow px-5 mt-4 mb-0 mx-0`}
                onClick={() => {
                  if (refreshOnCancel) {
                    if (typeof (refresh) === 'function') refresh()
                    else refreshPage({ refresh: true });
                  } else {
                    onClose({
                      setShow,
                      parentOncCloseFunction: onCloseFunction,
                    });
                  }
                  dispatch({ type: STATUS, status: null });
                }}
              >
                {cancelButtonTitle}
              </CButton>
            ) : null}
            {actionButtonTitle ? (
              <CButton
                className={`${actionButtonClass} text no-shadow px-5 mt-4 mb-0 ml-3 mr-0`}
                onClick={() => {
                  if (actionButtonEvent) {
                    actionButtonEvent();
                    setShow(false);
                  } else {
                    setShow(false)
                  }
                }
                }
              >
                {actionButtonTitle}
              </CButton>
            ) : null}{" "}
          </CModalFooter>
        </CModal>
      </CCol>
    </CRow>
  );
};

export default PopUp;
